import React, { Suspense } from "react";
import { Redirect, Route, Switch } from "react-router-dom";

const AppList = React.lazy(() => import("./app-list"));
const AppCollection = React.lazy(() => import("./app-collection"));

const Application = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <div>
      <Switch>
        <Route
          exact
          path={`${match.url}/`}
          render={(props) => <AppList {...props} />}
        ></Route>
        <Route
          path={`${match.url}/appcollection/:id`}
          render={(props) => <AppCollection {...props} />}
        ></Route>
        <Redirect to="/error" />
      </Switch>
    </div>
  </Suspense>
);
export default Application;
